import React from 'react'
import {useSpring, animated} from 'react-spring'
import SplitText from './splitText'
import * as easings from 'd3-ease'

const HeroSection = ({setLoadState, loadState}) => {

    const header = "I'm Prateek\n, UI/UX Designer \nand a Full-Stack Web Developer. \nSpecialized on creating amazing experiences\n, on web and mobile devices.".split("\n")

    const Shift = useSpring({
        to: {
            marginTop: 0
        }, 
        from: {
            marginTop: window.innerHeight/4
        }, 
        delay: 7400,
        config: {
            duration: 600,
            easing: easings.easeCubic
        }
    })

    return [
        <div className="hero">
            <animated.h1 className="hero-header" style={Shift}>
                <SplitText>
                    {header}
                </SplitText>
            </animated.h1>
        </div>
    ]
}

export default HeroSection